@use '@miq/styles/src/mixins' as *;

.miq-staff-view {
  --fs-xl: 1.725rem;
  --fs-lg: 1.5rem;
  --fs-md: 1.25rem;
  --icon-size: 20px;

  .miq-staff-view-header-wrapper {
    margin-bottom: var(--sp-1);

    .miq-staff-view-header {
      min-height: 50px;
      display: flex;
      align-items: center;
      justify-content: space-between;

      .miq-staff-view-header-title-text {
        font-size: var(--fs-md, 1.2rem);
      }
    }

    .miq-staff-view-header-text {
      @include break-word;

      color: var(--gray-600, gray);
      font-size: var(--fs-sm, 0.8rem);
    }
  }
}
