@use '@miq/styles/src/variables' as *;
@use '@miq/styles/src/mixins' as *;

.miq-staff-layout {
  --icon-size: 24px;

  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  overflow: hidden;
  display: flex;
  flex-grow: 1;
  max-height: 100%;
  height: 100%;

  .miq-staff-layout-main {
    // display: flex;
    // flex-direction: column;
    flex-grow: 1;
    position: relative;
    overflow-y: auto;
    background-color: var(--gray-50, white);
    padding-bottom: 60px;

    .miq-container-fluid {
      // flex-grow: 1;
      position: relative;
      padding: 0 0.5rem;
    }
  }

  .miq-staff-layout-sidebar {
    --fs-xxl: 1.5rem;
    --icon-size: 20px;

    position: relative;
    flex: 0 0 140px;
    // flex: 0 0 320px;
    // flex-basis: 210px;
    display: flex;
    flex-direction: column;
    min-width: 0;
    transform: translateX(0);
    padding-bottom: 1rem;

    @include lg-down {
      position: absolute;
      top: 0;
      left: 0;
      height: 100%;
      width: 260px;
      transition: transform 0.4s cubic-bezier(0.1, 0.7, 0.1, 1);
      transform: translate3d(-260px, 0, 0);
    }

    .miq-staff-layout-sidebar-body {
      flex-grow: 1;
      display: flex;
      flex-direction: column;
      justify-content: space-between;

      .miq-staff-layout-sidebar-body-content,
      .miq-staff-layout-sidebar-body-footer {
        display: flex;
        flex-direction: column;

        .miq-staff-nav-link {
          padding: 0.5rem;
          margin-right: 0.5rem;
          display: flex;
          align-items: center;
          position: relative;
          color: black;
          border-left: 0.3rem solid transparent;
          // text-transform: uppercase;

          &.active {
            font-weight: 600;
            border-left: 0.3rem solid var(--blue-600);
            background-color: var(--gray-200);
            border-top-right-radius: var(--border-radius, 4rem);
            border-bottom-right-radius: var(--border-radius, 4rem);
            // color: var(--blue-600);

            svg {
              fill: var(--blue-600);
            }
          }

          &:hover {
            background-color: var(--blue-50);
            // border-left: 3px solid var(--blue-100);
          }

          .label {
            font-size: 1rem;
            padding-left: 28px;
            display: block;
            max-width: 100%;
            overflow: hidden;
            text-overflow: ellipsis;
            white-space: nowrap;
          }

          svg {
            stroke-width: 3px;
            position: absolute;
            line-height: 1.5;
          }
        }
      }
    }
  }

  .miq-staff-layout-mobile-nav {
    display: none;

    .miq-staff-nav-link {
      display: flex;
      flex-direction: column;
      align-items: center;

      svg {
        display: block;
      }

      .label {
        font-weight: 700;
      }
    }

    @include md-down {
      position: fixed;
      bottom: 0;
      left: 0;
      right: 0;
      z-index: 9999;
      display: flex;
      align-items: center;
      justify-content: space-around;
    }
  }
}
